.wrapper {
  background: linear-gradient(45deg, rgb(14, 21, 56) 68%, rgb(43, 60, 144));
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-radius: 10px;
}
.logo {
  margin: 5px 0 0 20px;
  width: 70px;
  height: 70px;
  display: flex;
  align-items: center;
  & img {
    max-width: 100%;
    max-height: 100%;
  }
}
.login_block {
  display: flex;
  align-items: center;
  & button {
    font-size: 0.55em;
    font-weight: bold;
    width: 100px;
    height: 30px;
    border-radius: 0;
    margin: 0 0 0 20px
  }
}
.loggedUser {
  margin: 0 30px 0 0;
  font-size: 1.5em;
  font-weight: bold;
  color: #fff;
}
.loginButton {
  position: relative;
  display: inline-block;
  width: 100px;
  height: 30px;
  font-size: 14px;
  font-weight: bold;
  text-decoration: none;
  text-transform: uppercase;
  background-color: #FFF;
  border: none;
  & > span {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    //display: inline-block;
    background: #0e1538;
    z-index: 10;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff;
    font-size: 1.1em;
    letter-spacing: 2px;
    text-transform: uppercase;
    border: 1px solid #040a29;
    overflow: hidden;
    &:before {
      content: '';
      position: absolute;
      top: 0;
      left: -50%;
      width: 100%;
      height: 100%;
      background: rgba(255, 255, 255, 0.075);
      transform: skew(25deg);
    }
  }
  &:before, &:after {
    content: '';
    position: absolute;
    inset: 0;
    background: linear-gradient(45deg, #ff075b, #0e1538, #0e1538, #1aff22);
    transition: 0.5s;
  }
  &:hover {
    &:before {
      inset: -3px;
    }
    &:after {
      inset: -3px;
      filter: blur(10px);
    }
  }
}
